.ReviewsPage {
  position: relative;
  background-color: rgba(211, 211, 211, 0.3);
  padding: 2rem;
  height: 90vh;
  overflow-y: scroll;
  overflow-x: hidden;
}

.reviewsData {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  justify-content: space-evenly;
}

.reviewItem {
  padding: 1rem;
  border-radius: 1rem;
  border: 2px solid white;
  background-color: rgba(211, 211, 211, 0.3);
}

.reviewItem .itemDetailReview {
  padding: 1rem;
}
